<template></template>
<script setup>
import { reactive, ref, onMounted, computed } from "vue";
import { useStore } from 'vuex';
import IndexDB from '../untils/indexDB.js'
const store = useStore()
const arrey = reactive(computed(() => store.state.webArry)).value
const datas = JSON.stringify(arrey)
const test = async function () {

  const dbName = 'myDB', storeName = 'website'
  const db = await IndexDB.openDB(dbName, storeName, 1)
  var data = await IndexDB.addData(db, storeName, {
    id: 12, // 必须且值唯一
    test: datas
  }) // 新增数据
  console.log(data, '1')

  var data = await IndexDB.getDataByKey(db, storeName, 111) // 获取数据
  console.log(data, '2')

  var data = await IndexDB.updateDB(db, storeName, { id: 111, desc: '修改的内容' }) // 修改数据
  console.log(data, '3')

  // var data = await IndexDB.deleteDB(db, storeName, 111) // 删除数据
  // console.log(data, '4')
}

test()
</script>
